<template>
  <div class="materialList">
    <section class="con">
      <figure>
        <div class="crumbs">
          <span></span>
        </div>
      </figure>
    <div class="box" v-loading='load_data' element-loading-text="">
      <div class="matrialBtn">
        <el-button type="primary" @click="upLoad"></el-button>
        <el-button @click="newMateral"></el-button>
      </div>
      <el-form :inline="true" label-position='top'>
        <el-form-item label="">
          <el-input placeholder="" v-model="formData.materalName">
            <!-- <i slot="suffix" class="el-input__icon el-icon-search"></i> -->
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formData.materalType" placeholder="" @change="materalTypeChange"><br>
            <el-option v-for="item in formData.materalList" :key="item.value" :label="item.name" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formData.materalLabel" placeholder="" @change="materalLabeChange"><br>
            <el-option v-for="item in formData.materalLabelList" :key="item.tagId" :label="item.name" :value="item.tagId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style='margin-top:50px;'>
          <el-button type="primary" icon="el-icon-search" @click="searchHost"></el-button>
        </el-form-item>
        <el-form-item style='margin-top:50px;'>
          <el-button type="primary" @click="clearHost"></el-button>
        </el-form-item>
        <el-table :data="tableData">
          <el-table-column align="center" prop="name" label=""></el-table-column>
          <el-table-column align="center" prop="typeCN" label=""></el-table-column>
          <el-table-column align="center" prop="tagCN" label=""></el-table-column>
          <el-table-column align="center" label="(/">
            <template slot-scope="scope">
              <p v-if="scope.row.type !=5">{{scope.row.playCount}}/{{scope.row.planCount}}</p>
              <p v-if="scope.row.type ==5">-</p>
            </template>
          </el-table-column>
          <el-table-column align="center" label="(/">
            <template slot-scope="scope">
              <p v-if="scope.row.type !=5">
                <span v-if="scope.row.playTime !=''">{{scope.row.playTime}}</span>
                <span v-if="scope.row.playTime ==''">0</span>
                <span v-if="scope.row.planTime !=''">/{{scope.row.planTime}}</span>
                <span v-if="scope.row.planTime ==''">/0</span>
              </p>
              <p v-if="scope.row.type ==5">-</p>
            </template>
          </el-table-column>
          <el-table-column align="center" label="">
            <template slot-scope="scope">
              <el-button type="text" @click="edit(scope)"></el-button>
              <el-button type="text" @click="preview(scope.row)"></el-button>
              <router-link :to="{ name: 'CloudSeedPlan',query: {shopId: scope.row.shopId}}">
                <el-button type="text"></el-button>
              </router-link>
              <!-- <router-link :to="{ name: 'MaterialModify',query: {sid: scope.$index}}">
                <el-button type="text"></el-button>
              </router-link> -->
              <el-button type='text' @click="deletePlan(scope)"></el-button>
              <el-button type="text" @click="createPlans(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination :pageSize="page.pageRow" layout="total, prev, pager, next"
            :total="page.total" prev-text="" next-text="" @current-change="pageChange" :current-page.sync="page.pageNo"></el-pagination>
      </el-form>
    </div>
    <!--  -->
    <upload-material :diaLogChange="diaLogChange" :materalLabelList='formData.materalLabelList' @closeDia="closeDia"></upload-material>
    <!--  -->
    <materia-list :diaLogMateria="diaLogMateria" :materiaObject='materiaObject' @chooseMaterial="chooseMaterial"></materia-list>
    <!--    -->
    <preview  :previewMateria="previewMateria" :previewInfo='previewInfo' @closePreview='closePreview'></preview>
    <!--  -->
     <screen-list :screenDialog="screenDialog" :addScreenList="creatPlanDia.addScreenList" @closeScreen='closeScreen' :tabList='tabList' :openScreen='openScreen'></screen-list>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogEdit" :inline="true" :close-on-click-modal="false" :show-close="false">
      <el-form label-position="right" label-width="80px">
        <el-form-item label="" :required='true'>
          <el-input placeholder="" v-model="editMatrial.name" style="width:100%;margin-bottom:20px;">
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editMatrial.tagId" filterable allow-create placeholder="" style="width:100%;">
            <el-option v-for="item in formData.materalLabelList" :key="item.tagId" :label="item.name" :value="item.tagId"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" @click="editSava"></el-button>
        <el-button @click="editCancel"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog :title="cycleMateralGroup.title" :visible.sync="centerDialogVisible" :inline="true" :close-on-click-modal="false" :show-close="false">
      <el-form label-width="80px" label-position="right">
        <el-form-item label="" :required="true">
          <el-input placeholder="" style="width:100%;" v-model="cycleMateralGroup.name"></el-input>
        </el-form-item>
        <ul class="ulList">
          <li v-for="item,index) in cycleMateralGroup.options">
            <p class="fileList">
              <span>{{++index}}</span>
              <span>
                <span class='removeFileSpan' @click="remove(index)"></span>
                <span class='removeFileSpan' v-if="index !== 1" @click="moveUp(index)"></span>
                <span class='removeFileSpan' v-if="index !== cycleMateralGroup.options.length" @click="moveDown(index)"></span>
              </span>
            </p>
            <el-form label-width="80px" label-position="right">
              <el-form-item label="">
                <span>{{item.name}}</span>
              </el-form-item>
              <el-form-item  label="">
                <el-input type="number" v-model="item.hour" oninput="if(value.length>2)value=value.slice(0,2)" style="width:50px;"></el-input>
                <el-input type="number" v-model="item.minute" oninput="if(value.length>2)value=value.slice(0,2)" style="width:50px;"></el-input>
              </el-form-item >
            </el-form>
          </li>
        </ul>
      </el-form>
      <el-button class="addBtn" icon="el-icon-plus" @click="addMaterial"></el-button>
      <span slot="footer">
        <el-button type="primary" @click="nowCreate">{{cycleMateralGroup.btnText}}</el-button>
        <el-button @click="cancelCreate"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="createPlan" :inline="true" :close-on-click-modal="false" :show-close="false">
      <el-form label-position="right" label-width='80px'>
        <el-form-item style="border-bottom:1px solid #E4E7E9;">
          <span>{{creatPlanDia.diaMateraName}}</span>
          <el-button size="mini">{{creatPlanDia.diaMateraType}}</el-button>
        </el-form-item>
        <el-form-item label=":">
            <el-radio-group v-model="creatPlanDia.playType">
              <el-radio :label=0></el-radio>
              <el-radio :label=1></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=":" style="border-bottom:1px solid #E4E7E9;">
            <div class="rightTime">
              <el-date-picker style="width:100%;margin-bottom:10px;"
                v-model="creatPlanDia.beginTime"
                type="daterange"
                range-separator="-"
                start-placeholder=""
                end-placeholder="">
              </el-date-picker>
              <el-time-picker style="width:100%;"
                is-range
                v-model="creatPlanDia.endTime"
                range-separator="-"
                start-placeholder=""
                end-placeholder=""
                placeholder="">
              </el-time-picker>
            </div>
          </el-form-item>
          <el-form-item label="" style="margin-top:10px;">
            <el-button @click="addScreen"></el-button><br>
            <el-tag v-for="item in creatPlanDia.addScreenList">{{item.screenName}}</el-tag>
          </el-form-item> 
      </el-form>
      <span slot="footer">
          <el-button type="primary" @click="savaCreat"></el-button>
          <el-button @click="cancelCreat"></el-button>
        </span>
    </el-dialog>
    </section>
  </div>
</template>
<script>
import * as Api from "@/api/api1"
const Swal=require("sweetalert2")
const lodash = require('lodash')
import UploadMaterial from "@/components/uploadMaterial"
import MaterialList from "@/components/materialList"
import Preview from "@/components/preview"
import ScreenList from '@/components/screenList'
let moment = require('moment')
  export default{
    name:"MaterialManageList",
    components:{
      "uploadMaterial":UploadMaterial,
      'materiaList':MaterialList,
      'preview':Preview,
      'screenList':ScreenList
    },
    data(){
      return{
        load_data:true,
        diaLogChange:false, // 
        centerDialogVisible:false,//
        createPlan:false,//
        diaLogMateria:false, //
        shopId:'',
        materiaObjects:{},
        materiaObject:{},
        dialogEdit:false, //
        previewMateria:false, // 
        screenDialog:false, //
        page:{
          pageRow:0,
          pageNo:1,
          totalPageNo:0,
          total:0
        },
        openScreen:{
          saveScreenStyle:false,//
          screenStyle:'',// newOpen  copyDay copyWeek
          screeName:'',//name
          screeId:'',// ID
          operationShopId:''
        },
        tabList:[],
        creatPlanDia:{
          shopId:'',
          materialId:'',
          diaMateraName:'',//
          diaMateraType:'',//
          type:'',
          playType:0,
          beginTime:[new Date(),new Date()],
          addScreenList:[],
          endTime:[new Date(),new Date()],
          screenIds:[]
        },
        
        previewInfo:{},
        tabIndex:'',
        formData:{
          materalName:'',
          materalType:'',
          materalLabel:'',
          materalList:[
            {
              value: '0',
              name: ''
            },
            {
              value: '2',
              name: ''
            },
            {
              value: '1',
              name: ''
            },
            {
              value: '3',
              name: ''
            },
            {
              value: '4',
              name: ''
            },
            {
              value: '5',
              name: ''
            }
          ],
          materalLabelList:[]
        },
        editMatrial:{
          materialId:'',
          tagCN:'',
          name:'',
          url:'',
          type:'',
          tagId:''
        },
        tableData:[],
        cycleMateralGroup:{
          title:'',
          btnText:'',
          name:'',
          materialId:'',
          options:[]
        },
      }
    },
    mounted() {
      this.load_data= true
      this.findMaterials(1)
      this.findTags()
    },
    methods: {
      //
      edit: lodash.debounce(function(scope){
        this.tabIndex = scope.$index
        if(scope.row.type == 5){//
          this.centerDialogVisible = true
          this.load_data = true
          this.loadMaterial(scope.row.materialId,'')
        }else{
          this.dialogEdit = true
          this.editMatrial.materialId = scope.row.materialId
          this.editMatrial.name = scope.row.name
          this.editMatrial.tagCN = scope.row.tagCN
          this.editMatrial.tagId = scope.row.tagId
          this.editMatrial.type = scope.row.type
          this.loadMaterial(scope.row.materialId,'1')
        }
      },300),
      //
      deletePlan(scope){
        this.tabIndex = scope.$index
        this.$confirm('','',{
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() =>{
          this.load_data = true
          this.delPlanByMaterial(scope.row.materialId,true)
        })
      },
      //
      preview: lodash.debounce(function(scope){
        this.loadMaterial(scope.materialId)
      },300),
      //
      pageChange(val){
        this.page.pageNo = val
        this.findMaterials(val)
      },
      //
      searchHost: lodash.debounce(function(){
        this.load_data = true
        this.page.pageNo = 1
        this.findMaterials(1)
      },300),
      clearHost: lodash.debounce(function(){
        this.page.pageNo = 1
        for(var i in this.formData){
          if(i == 'materalLabelList'){
            this.formData[i] = this.formData[i]
          }else   if(i == 'materalList'){
            this.formData[i] = this.formData[i]
          }else{
            this.formData[i] =''
          }
        }
        this.load_data = true
        this.findMaterials(1)
      },300),
      //
      materalLabeChange: lodash.debounce(function(){
        this.load_data = true
        this.page.pageNo = 1
        this.findMaterials(1)
      },300),
      materalTypeChange(){
        this.load_data = true
        this.page.pageNo = 1
        this.findMaterials(1)
      },
      //
      createPlans(scope){
        this.createPlan = true
        this.creatPlanDia.shopId = scope.shopId
        this.creatPlanDia.materialId = scope.materialId
        this.creatPlanDia.diaMateraName = scope.name
        this.creatPlanDia.type = scope.type
        this.creatPlanDia.diaMateraType = scope.typeCN
      },
      savaCreat:lodash.debounce(function(){
        // this.openScreen.saveScreenStyle = true
        let startDate = moment(this.creatPlanDia.beginTime[0].toUTCString()).format('YYYY-MM-DD')
        let endDate = moment(this.creatPlanDia.beginTime[1].toUTCString()).format('YYYY-MM-DD')
        let startTime = moment(this.creatPlanDia.endTime[0].toUTCString()).format('HH:mm')
        let endTime =moment(this.creatPlanDia.endTime[1].toUTCString()).format('HH:mm')
        function DateMinus(date1,date2){
        var sdate = new Date(date1); 
        var now = new Date(date2); 
        var days = now.getTime() - sdate.getTime(); 
        var day = parseInt(days / (1000 * 60 * 60 * 24)); 
        return day; 
        }
        let timeSub = DateMinus(startDate,endDate)
        if(timeSub > 59){
          Swal({
            text:'60',
            allowOutsideClick:false
          })
        }else if(endTime === startTime){
          Swal({
            text:'',
            allowOutsideClick:false
          })
        }else if(this.creatPlanDia.screenIds.length ===0){
          Swal({
            text:'',
            allowOutsideClick:false
          })
        }else{
          this.load_data = true
          this.SavePlan(startDate,endDate,startTime,endTime)
        }
      },300),
      cancelCreat(){
        this.openScreen.saveScreenStyle = true
        this.createPlan = false
        for(let i in this.creatPlanDia){
          if(i ==='addScreenList' || i ==='screenIds'){
            this.creatPlanDia[i] =[]
          }
          else if(i === 'endTime' || i === 'beginTime'){
            this.creatPlanDia[i] =[new Date(),new Date()]
          }else{
            this.creatPlanDia[i] = ''
          }
          
        }
      },
      //
      addScreen: lodash.debounce(function(){
        this.$nextTick(() => {
          this.screenDialog = true
          this.openScreen.saveScreenStyle = false
          this.openScreen.screenStyle = 'newOpen'
          this.openScreen.screeName = ''
          this.openScreen.screeId = '-1'
          this.openScreen.operationShopId =''
          this.screenFindPage(1)
        })
      },300),
      editSava: lodash.debounce(function(){
        if(this.editMatrial.name === ''){
          this.$message({
            message: '',
            type: 'warning'
          })
        }else{
          this.load_data = true
          if(this.editMatrial.tagId){
            this.findTags(this.editMatrial.tagId)
          }else{
            let obj ={
              materialId:this.editMatrial.materialId,
              name:this.editMatrial.name,
              type:this.editMatrial.type,
              url:this.editMatrial.url
            }
            this.saveMaterial(obj)
          }
        }
      },300),
      editCancel(){
         this.dialogEdit = false
         for(let i in this.editMatrial){
           this.editMatrial[i] =''
         }
      },
      remove(index){
         let num = index -1
        this.cycleMateralGroup.options.splice(num,1)
      },
      //
      moveUp(index){
       let num = index -1
       let num2 = index - 2
       let obj = this.cycleMateralGroup.options[num]
       this.cycleMateralGroup.options.splice(num,1)
       this.cycleMateralGroup.options.splice(num2,0,obj)
      },
      //
      moveDown(index){
        let num = index -1
        let obj = this.cycleMateralGroup.options[num]
        this.cycleMateralGroup.options.splice(num,1)
        this.cycleMateralGroup.options.splice(index,0,obj)
      },
      newMateral(){ //
        this.centerDialogVisible = true
        this.cycleMateralGroup.title = ''
        this.cycleMateralGroup.btnText = ''
      },
      nowCreate:lodash.debounce(function(){ //
        if(this.cycleMateralGroup.name === ''){
          this.$message({
            message: '',
            type: 'warning'
          })
        }else if(this.cycleMateralGroup.options.length === 0){
          this.$message({
            message: '',
            type: 'warning'
          })
        }else{
          let noConditions = 0
          for(let i=0;i<this.cycleMateralGroup.options.length;i++){
            if(this.cycleMateralGroup.options[i].hour === '' && this.cycleMateralGroup.options[i].minute === ''){
              this.$message({
                message: '',
                type: 'warning'
              })
              noConditions = 1
              break
            }else if(this.cycleMateralGroup.options[i].hour === 0 && this.cycleMateralGroup.options[i].minute === 0){
              this.$message({
                message: '0',
                type: 'warning'
              })
              noConditions = 1
              break
            }else if(this.cycleMateralGroup.options[i].minute > 59){
              this.$message({
                message: '59',
                type: 'warning'
              })
              noConditions = 1
              break
            }else if(this.cycleMateralGroup.options[i].hour >23){
              this.$message({
                message: '24',
                type: 'warning'
              })
              noConditions = 1
              break
            }

          }
          if(noConditions === 0){
            this.load_data = true
            let obj ={}
              obj.name=this.cycleMateralGroup.name
              obj.options = this.cycleMateralGroup.options
              obj.materialId = this.cycleMateralGroup.materialId
              this.saveCircleMaterial(obj)
          }
        }
      },300),
      cancelCreate(){
        this.centerDialogVisible = false
         for(let i in this.cycleMateralGroup){
           if(i == 'options'){
             this.cycleMateralGroup[i] = []
           }else{
             this.cycleMateralGroup[i] = ''
           }
            
          }
      },
      addMaterial(){ //
      this.diaLogMateria = true
      this.materiaObject = this.materiaObjects
      this.materiaObject.addMaterial =''
      },
      upLoad(){ // 
        this.diaLogChange= true
      },
      closeDia(value){
        this.diaLogChange= value
        this.findMaterials(1)
      },
      closePreview(value){
         this.previewMateria= value
      },
      closeScreen(list,value){
        this.screenDialog= value
        for(let i in list){
          this.creatPlanDia.screenIds.push(list[i].screenId)
        }
        this.creatPlanDia.addScreenList=list
      },
      //
      chooseMaterial: lodash.debounce(function(value,tas){
        if(tas){
          let obj = {}
          obj.materialId =tas.materialId
          obj.name = tas.name
          obj.hour = 0
          obj.minute = 0
          this.cycleMateralGroup.options.push(obj)
        }
        this.diaLogMateria = value
      },300),
      //
      saveMaterial(obj){
        let self = this
        Api.saveMaterial(obj).then((res) =>{
          let data = res.data
          if(data.status === 0){
            self.load_data = false
            self.dialogEdit = false
            if(self.tabIndex !==''){
              self.tableData[self.tabIndex].name = self.editMatrial.name
              self.tableData[self.tabIndex].tagCN = self.editMatrial.tagCN
               self.tableData[self.tabIndex].tagId = self.editMatrial.tagId
            }
            self.$message({
              message: '',
              type: 'success'
             })
          }else{
            self.load_data = false
            Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
          for(let i in self.editMatrial){
              self.editMatrial[i] = ''
            }
        }).catch((error) =>{
         self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      },
      //
      saveCircleMaterial(obj){
        let self =this
        Api.saveCircleMaterial(obj).then((res) =>{
          let data =res.data
          if(data.status === 0){
            self.tableData.push(obj)//
            self.load_data = false   
            self.centerDialogVisible = false
            self.cycleMateralGroup.name = ''
            self.cycleMateralGroup.options= []
            self.findMaterials(self.page.pageNo)
            if(self.cycleMateralGroup.title == ''){
              self.$message({
                message: '',
                type: 'success'
              })
            }else{
              self.$message({
                message: '',
                type: 'success'
              })
            }
          }
        }).catch((error) =>{
          self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      },
      //
      findMaterials(pageNo){
        let self =this
        let obj ={
          name:self.formData.materalName,
          type:self.formData.materalType,
          tagId:self.formData.materalLabel,
          pageNo:pageNo
        }
        Api.findMaterials(obj).then((res) =>{
          let data =res.data
          if(data.status === 0){
            self.load_data = false
            if(data.object){
              self.page.pageRow = data.object.pageRow
              self.page.pageNo = data.object.pageNo
              self.page.total = data.object.total
              self.page.lastPage = data.object.lastPage
              self.tableData = data.object.objects
              if(data.object.pageNo === 1){
                self.materiaObjects = data.object
              }
            }else{
              self.page.pageRow =20
              self.page.pageNo = 1
              self.page.total = 0
              self.tableData = []
            }
          }else{
            self.load_data = false
            Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
        }).catch((error) =>{
          self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      },
      //
      findTags(name){
        let self =this
        Api.findTags().then((res) =>{
          let data =res.data
          if(data.status === 0){
            self.load_data = false
            self.formData.materalLabelList = data.object
            let noCon = 0
            if(name != undefined){
              for(let i in data.object){
                if(name === data.object[i].name){
                  self.editMatrial.tagId = data.object[i].tagId
                  self.editMatrial.tagCN = data.object[i].name
                  self.saveMaterial(self.editMatrial)
                  noCon = 1
                  break;
                }else if(name === data.object[i].tagId){
                  self.editMatrial.tagCN = data.object[i].name
                  self.saveMaterial(self.editMatrial)
                  noCon =1
                   break;
                }
              }
              if(noCon === 0){
                self.saveTag(name)
              }
            }
          }else{
            self.load_data = false
            Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
        }).catch((error) =>{
          self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      },
      //
      loadMaterial(materialId,name){
        let self = this
        Api.loadMaterial(
          {materialId:materialId}
        ).then((res) =>{
          let data = res.data
          if(data.status === 0){
            self.load_data = false
            if(name == undefined){//
              if(data.object){
                if(data.object.type === 3){
                  window.open(data.object.url)
                }else if(data.object.type === 4){
                  self.$message({
                    message: '',
                    type: 'warning'
                  })
                }else if(data.object.type === 0 || data.object.type === 1 || data.object.type === 2){
                  self.previewMateria = true
                  self.previewInfo= data.object
                }else{
                  
                  if(data.object.options.length === 0){
                    Swal({
                      text:'',
                      allowOutsideClick:false
                    })
                  }else{
                    self.previewMateria = true
                    self.previewInfo=data.object
                  }
                }
              }else{
                Swal({
                  text:'',
                  allowOutsideClick:false
                })
              }
            }else{//
              if(name == ''){
                self.cycleMateralGroup.name = data.object.name
                self.cycleMateralGroup.title = ''
                self.cycleMateralGroup.btnText = ''
                self.cycleMateralGroup.materialId = data.object.materialId
                if(data.object.options.length>0){
                  self.cycleMateralGroup.options = data.object.options
                }
              }else{
                self.editMatrial.url = data.object.url
              }
              
            }
            
          }else{
            self.load_data = false
              Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
        }).catch((error)=>{
          self.load_data = false
              Swal({
              text:'',
              allowOutsideClick:false
            })
        })
      },
      //
      SavePlan(startDate,endDate,startTime,endTime){
        let self = this
         Api.SavePlan(
           {
             shopId:self.creatPlanDia.shopId,
            // shopId:1,
             materialId:self.creatPlanDia.materialId,
             type:self.creatPlanDia.type,
             startDate:startDate,
             endDate:endDate,
             startTime:startTime,
             endTime:endTime,
             screenIds:self.creatPlanDia.screenIds
           }
         ).then((res) =>{
           let data = res.data
           if(data.status === 0){
             self.load_data = false
             self.createPlan = false
             self.$message({
              message: '',
              type: 'success'
             })
           }else{
             self.load_data = false
              Swal({
                text:data.errorMsg,
                allowOutsideClick:false
              })
           }
         }).catch((error) =>{
           self.load_data = false
            Swal({
              text:'',
              allowOutsideClick:false
            })
         })
      },
      //
      screenFindPage(pageNo){
        let self = this
        Api.screenFindPage(
          {
            pageNo:pageNo
          }
        ).then((res) =>{
          let data = res.data
          if(data.status === 0){
            self.load_data = false
            if(data.object){
              self.tabList = data.object
            }
          }else{
            self.load_data = false
            Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
        }).catch((error) =>{
          self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      },
      //
      delPlanByMaterial(materialId,groupFlag){
        let self = this
        Api.delPlanByMaterial(
          {
            materialId:materialId,
            groupFlag:groupFlag
          }
        ).then((res) =>{
          let data = res.data
          if(data.status === 0){
            self.load_data = false
            self.findMaterials(self.page.pageNo)
            // self.tableData.splice(self.tabIndex,1)
          }else{
            self.load_data = false
            Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
          self.tabIndex = ''
        }).catch((error)=>{
          self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      },
      //
      saveTag(name){
        let self =this
        Api.saveTag({name:name}).then((res) =>{
          let data = res.data
          if(data.status === 0){
            self.findTags(name)
          }else{
            self.load_data = false
            Swal({
              text:data.errorMsg,
              allowOutsideClick:false
            })
          }
        }).catch((error) =>{
          self.load_data = false
          Swal({
            text:'',
            allowOutsideClick:false
          })
        })
      }
    }
  }
</script>
<style type="text/scss" lang="scss">
.materialList{
  .matrialBtn{
    margin-bottom: 20px;
  }
  .el-dialog{
    .el-form-item{
      margin-bottom: 0;
    }
    .ulList{
      max-height: 400px;
      overflow-y: scroll;
      ::-webkit-scrollbar {
        width: 1px;
      }
      li{
        .fileList{
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          background-color: #fff;
          padding: 10px 0;
          .removeFileSpan:hover{
            cursor: pointer;
            color:#409EFF;
          }
        }
        .el-form{
          background-color: #F5F7FA;
          margin-bottom: 10px;
        }
      }
    }
    .addBtn{
      width: 100%;
      margin-top: 10px;
    }
    .rightTime{
      margin-bottom: 10px;
    }
  }
  .el-table th{
    background-color: #F5F5F6;
  }
}
</style>


