export function Kind(val) {
  if(val==0) {
    return ''
  }else if(val == 1){
    return ''
  }else if(val == 2){
    return ''
  }else if(val == 3){
    return ''
  }else if(val == 4){
    return 'NN'
  }else if(val == 5){
    return ''
  }else if(val == 6){
    return ''
  }
}

export function Send(val) {
  if(val==0||val==""){
    return '[]'
  }else{
    return '[]'
  }
}

export function CouponStatus(val) {
  if(val==0){
    return '[]'
  }else if(val==1){
    return '[]'
  }
}



