<template>
  <div class="test">
    <section class="con">
    <div class="box">
      <el-tag v-for="item in multipleSelectionAll">
            {{item.name}}
          </el-tag>
       <!--  -->
      <el-table :data="tableData3" ref="multipleTable" stripe style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="barcode" label=""></el-table-column>
        <el-table-column prop="quantity" label=""></el-table-column>
      </el-table>
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="page.currentPage" :page-size="page.pnum" layout="total, prev, pager, next , jumper" :total="page.pageTotal"></el-pagination>
    </div>
    <el-select v-model="value7" placeholder="">
        <el-option-group
          v-for="group in options3"
          :key="group.label"
          :label="group.label">
          <el-option
            v-for="item in group.options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-option-group>
      </el-select>
    </section>
  </div>
</template>
<script>
  export default{
    name:'Test',
    data(){
      return{
        tableData3: [], //  
        multipleSelectionAll:[],//
        multipleSelection:[],// 
        idKey: 'id', // 
        page:{
            //
            pnum:2,
            //
            pageTotal:3,
            //1
            currentPage:1,
        },
        tab4:[{
          id:'0',
          date: '2016-05-03',
          name: '',
          address: ' 118 '
          }, {
            id:'1',
            date: '2016-05-06',
            name: '',
            address: ' 151 '
          }
        ],
        tableData5:[
          {
            id: '2',
            date: '2016-05-03',
            name: '',
            address: ' 151 '
          },{
            id: '3',
            date: '2016-05-05',
            name: '55',
            address: ' 151 '
          }
        ],
        tab6:[
          {
          id:'0',
          date: '2016-05-03',
          name: '',
          address: ' 118 '
          },
          {
            id: '2',
            date: '2016-05-03',
            name: '',
            address: ' 151 '
          }
        ],
        options3: [{
          label: '',
          options: [{
            value: 'Shanghai',
            label: ''
          }, {
            value: 'Beijing',
            label: ''
          }]
        }, {
          label: '',
          options: [{
            value: 'Chengdu',
            label: ''
          }, {
            value: 'Shenzhen',
            label: ''
          }, {
            value: 'Guangzhou',
            label: ''
          }, {
            value: 'Dalian',
            label: ''
          }]
        }],
        value7: ''
      }
    },
    created() {
      this.multipleSelectionAll = this.tab6
      this.getGoodsList()
    },
    methods:{
      handleCurrentChange(val){
          this.changePageCoreRecordData();
          this.getGoodsList(val);
      },
       handleSelectionChange (val) {
          // table,@selection-change="handleSelectionChange"
          this.multipleSelection = val;
           setTimeout(()=>{
              this.changePageCoreRecordData();
          }, 50)
      },
      // 
      setSelectRow() {
          if (!this.multipleSelectionAll || this.multipleSelectionAll.length <= 0) {
              return;
          }
          // 
          let idKey = this.idKey;
          let selectAllIds = [];
          let that = this;
          this.multipleSelectionAll.forEach(row=>{
              selectAllIds.push(row[idKey]);
          })
          this.$refs.multipleTable.clearSelection();
          for(var i = 0; i < this.tableData3.length; i++) {
              if (selectAllIds.indexOf(this.tableData3[i][idKey]) >= 0) {
                  // tableref="table"
                  this.$refs.multipleTable.toggleRowSelection(this.tableData3[i], true);
              }
          }
      } ,
      // 
      changePageCoreRecordData () {
          // 
          let idKey = this.idKey;
          let that = this;
          // 
          if (this.multipleSelectionAll.length <= 0) {
              this.multipleSelectionAll = this.multipleSelection;
              return;
          }
          // key
          let selectAllIds = [];
          this.multipleSelectionAll.forEach(row=>{
              selectAllIds.push(row[idKey]);
          })
          let selectIds = []
          // id
          this.multipleSelection.forEach(row=>{
              selectIds.push(row[idKey]);
              // 
              if (selectAllIds.indexOf(row[idKey]) < 0) {
                  that.multipleSelectionAll.push(row);
              }
          })
          let noSelectIds = [];
          // id
          this.tableData3.forEach(row=>{
              if (selectIds.indexOf(row[idKey]) < 0) {
                  noSelectIds.push(row[idKey]);
              }
          })
          noSelectIds.forEach(id=>{
              if (selectAllIds.indexOf(id) >= 0) {
                  for(let i = 0; i< that.multipleSelectionAll.length; i ++) {
                      if (that.multipleSelectionAll[i][idKey] == id) {
                          // 
                          that.multipleSelectionAll.splice(i, 1);
                          break;
                      }
                  }
              }
          })
      },
      getGoodsList(val){
        this.page.currentPage = val
        if(val === 2){
            this.tableData3 = this.tableData5
          }else{
            this.tableData3 = this.tab4
          }
        setTimeout(()=>{
            this.setSelectRow();
        }, 50)
      }
    }
  }
</script>


